<script>
	//<!--<link rel="stylesheet" href="https://www.amcharts.com/lib/3/plugins/export/export.css" type="text/css" media="all" />-->

	import 'amcharts3'
	import 'amcharts3/amcharts/serial'

    import outcomeService from '@/services/outcome-service'
    import incomeService from '@/services/income-service'

	export default {
		data () {
			return {
				income: {
					data: [],
					categories: [],
				},
				outcome: {
					data: [],
					categories: [],
				}
            }
		},

        computed: {
			monthData: function () {
				const data = []
				const income = this.$data.income.data
				const outcome = this.$data.outcome.data
				const timestamps = {
					start: +new Date(),
                    end: +new Date()
                }

				if (income.length) {
					timestamps.start = Math.min(income[0].date)
					timestamps.end = Math.max(income[income.length-1].date)
				}

				if (outcome.length) {
					timestamps.start = Math.min(outcome[0].date)
					timestamps.end = Math.max(outcome[outcome.length-1].date)
				}
				console.log(timestamps)

                const mon = []
				outcome.forEach(item => {
					const date = new Date(item.date)
					mon[date.getFullYear()] = mon[date.getFullYear()] || []
					mon[date.getFullYear()][date.getMonth()] = mon[date.getFullYear()][date.getMonth()] || 0
					mon[date.getFullYear()][date.getMonth()] += item.amount
				})

				income.forEach(item => {
					const date = new Date(item.date)
					mon[date.getFullYear()] = mon[date.getFullYear()] || []
					mon[date.getFullYear()][date.getMonth()] = mon[date.getFullYear()][date.getMonth()] || 0
					mon[date.getFullYear()][date.getMonth()] += item.amount
				})

                console.log(mon)

				for (let year in mon) {
					for (let month in mon[year]) {
						console.log(mon[year][month])
                        data.push({
                            income: 0,
                            outcome: mon[year][month],
                            month: month + " / " + year
                        })
					}
				}

				return data
            }
        },


		created: function () {
			outcomeService.events.$on('updated', (data) => {
				this.$data.outcome.data = data.outcome
				this.$data.outcome.categories = data.categories
			})

			incomeService.events.$on('updated', (data) => {
				this.$data.income.data = data.income
				this.$data.income.categories = data.categories
			})

			this.$data.outcome.data = outcomeService.getOutcomeArray()
			this.$data.outcome.categories = outcomeService.getCategories()

			//this.$data.income.data = incomeService.getIncomeArray()
			//this.$data.income.categories = incomeService.getCategories()
		},

		mounted () {
			var chart = window.AmCharts.makeChart("inOutChart", {
				"type": "serial",
				"svgIcons": false,
				"categoryField": "month",
				"columnWidth": 0.7,
				"plotAreaFillAlphas": 0.2,
				"plotAreaFillColors": "#FFF",
				"colors": [
					"#FFFFFF",
					"#444"
				],
				"color": "#222222",
				"creditsPosition": "bottom-right",
				"fontSize": 10,
				"theme": "default",
				"categoryAxis": {
					"gridPosition": "start"
				},
				"chartCursor": {
					"enabled": true,
					"balloonPointerOrientation": " vertical",
					"cursorColor": "#FFF",
					"color": "#222222",
					"graphBulletAlpha": 0,
					"graphBulletSize": 1,
					"selectionAlpha": 0.24
				},
				"chartScrollbar": {
					"enabled": true,
				},
				"trendLines": [],
				"graphs": [
					{
						"balloonText": "[[value]]: [[title]] [[category]] ",
						"fillAlphas": 1,
						"id": "AmGraph-1",
						"title": "Income",
						"type": "column",
						"valueField": "income"
					},
					{
						"balloonText": "[[value]]: [[title]] [[category]] ",
						"fillAlphas": 1,
						"gapPeriod": 3,
						"id": "AmGraph-2",
						"title": "Outcome",
						"type": "column",
						"valueField": "outcome"
					}
				],
				"guides": [],
				"valueAxes": [
					{
						"id": "ValueAxis-1",
						"stackType": "regular",
						"title": "$"
					}
				],
				"allLabels": [],
				"balloon": {},
				"legend": {
					"enabled": true,
					"useGraphSettings": true
				},
				"titles": [
					{
						"id": "Title-1",
						"size": 18,
						"text": "Income / Outcome per month",
                        "color": '#FFF',
						"fontFamily": "Segoe UI, arial",
					}
				],
				"dataProvider": this.monthData
			});
		},

		methods: {

		}
	}


</script>

<template>
    <div id="inOutChart"></div>
</template>

<style scoped>
    #inOutChart {
        width		: 100%;
        height		: 500px;
        font-size	: 11px;
